.nav_bg{
  background-color: #d19524;
}
.text_style{
  color: #000000
}
.header{
  height: 470px;
  background-color: #8e44ad;
}
.text_wrapper{
  background-color: #d19524;
  font-size: 1em;
  box-shadow: 2px 2px 2px 2px black;
}
.img_prop{
  width: 80%;
  height: fit-content;
  box-shadow: 2px 2px 2px 2px black;
}
.animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
.bg_color{
  background-color: #8e44ad;
  font-weight: 500;
  color: #000000;
}
@media only screen and (max-width: 960px) {
.header {
    height: 600px;
  }
}
@media only screen and (max-width: 720px) {
  .header {
      height: 560px;
    }
  }
  @media only screen and (max-width: 576px) {
    .header {
        height: 750px;
      }
    }
  body{
    background: #8e44ad !important;
  }
  .btn_color{
    background-color: #d19524 !important;
  }
  .btn_color:hover{
    background-color: #e4c17f !important;
  }
  .text_strength{
    font-weight: 600;
  }